
import { defineComponent, computed, onMounted, ref } from 'vue'
import CardList from '../components/CardList.vue'
import AddButton from '../components/AddButton.vue'
import { useStore } from 'vuex'
import { GlobalDataProps } from '../store'
import axios from 'axios'
import router from '@/router'

export default defineComponent({
  components: {
    CardList,
    AddButton
  },
  setup () {
    const resdatalength = ref(0)
    const page = ref(1)
    const searchval = ref('')
    const store = useStore<GlobalDataProps>()
    const queslist = computed(() => store.state.questions)
    const loginState = ref(localStorage.getItem('isLogin') === 'true')
    console.log(queslist.value, 'q')
    onMounted(() => {
      store.dispatch('fetchQuestions')
      axios.get('/questions').then(res => {
        resdatalength.value = res.data.length
        console.log(resdatalength.value, 'length')
      })
    })
    const changepage = (num: number) => {
      page.value += num
      store.dispatch('fetchQuestions', page.value)
    }
    const toSearch = () => {
      store.dispatch('fetchQuestionsByQ', searchval.value)
    }
    const toPost = () => {
      router.push('/createpage')
    }
    return {
      queslist,
      changepage,
      page,
      searchval,
      toSearch,
      resdatalength,
      toPost,
      loginState
    }
  }
})
